
import { LineLayer, PathLayer, ScatterplotLayer, TextLayer, PolygonLayer } from '@deck.gl/layers'
import PubSub from "pubsub-js"
import Map from './index'
import Topnav from "../../../topnav/index"

export default class Layer{
    constructor(){
        this.receiveDataInfo()
        this.receiveButtonInfo()
        this.receiveChangeMapLayers()
        Layer.updateHighlightPoint_Edge = this.updateHighlightPoint_Edge.bind(this)
        Layer.cancelHighlight = this.cancelHighlight.bind(this)
        Layer.updateHighlightEdge = this.updateHighlightEdge.bind(this)
    }
    //
    receiveButtonInfo = ()=>{
        PubSub.subscribe('button', (_, stateObj) => {// {dataKind, edgeKind, plane, netflow }
            let {button} = stateObj
            this.buttonState = button
            console.log("",button)
        });
        this.buttonState = Topnav.state
    }

    receiveDataInfo = ()=>{
        PubSub.subscribe('data', (_, stateObj) => {// {dataKind, edgeKind, plane, netflow }
            let {results, netflowResults} = stateObj
            this.results = results
            this.netflowResults = netflowResults
        });
    }

    receiveChangeMapLayers=()=>{
        PubSub.subscribe('changeMapLayers', (_, stateObj) => {
            let {changeMapLayers} = stateObj
            this.changeMapLayers = changeMapLayers
        });
    }


    //layer
    getPathLayer = (paramObj) => {//PathLayerd.path, d.color
        let {data, layerID, opacity, filter2D} = paramObj
        //2D
        if(filter2D)data = this.filter2D(data)
        opacity = opacity === undefined ? 0.2 : opacity
        return new PathLayer({
            id: layerID,
            data: [...data],
            pickable: true,
            widthScale: 1,
            widthMinPixels: 0.1,
            widthMaxPixels: 3,
            getPath: d => { return d.path },
            getColor: d => d.color,
            getWidth: d => { return d.width },
            opacity: opacity,
            billboard: true,
            autoHighlight: true,
            highlightColor: [255, 0, 0],
            onClick: (d) => { this.showInfo(d) }//this.convertEdge(d)  this.showInfo(d)
        });
    }

    getScatterPlotLayer = (paramObj) => {//ScatterPlotLayerd.fillColor, d.position, d.radius,
        let {data, layerID} = paramObj
        return new ScatterplotLayer({
            id: layerID,
            data: [...data],
            getFillColor: d => d.fillColor,
            getLineColor: [0, 0, 0],
            getPosition: d => d.position,//[a[0]*6, a[1]*4,a[2]]},//{ return [...d.xyz] },
            getRadius: d => { return d.radius },
            lineWidthMinPixels: 0,
            radiusMaxPixels: 100,
            radiusMinPixels: 1,
            radiusScale: 1,
            stroked: false,
            opacity: 1,
            pickable: true,
            autoHighlight: true,
            highlightColor: [0, 255, 0],
            onHover: (info) => this.highlightPoint_Edge(info),
            onClick: (d,event) => {this.clickScatter(d, event)}
        })
    }

    getTextLayer = (paramObj) => {//TextLayerd.position, d.name
        let {data, layerID} = paramObj
        return new TextLayer({
            id: layerID,
            data: [...data],
            pickable: false,
            getPosition: d => { let a = d.position; return [a[0], a[1], a[2] + 2] },
            getText: d => { return d.info.split(",")[0] },
            characterSet: "auto",
            getSize: 10,
            getAngle: 0,
            sizeScale: 1.2,
            sizeMinPixels: 10,
            //sizeMaxPixels:1,
            getTextAnchor: 'middle',
            getAlignmentBaseline: 'center'
        });
    }

    getPolygonLayer = (paramObj) => {
        let {data, layerID, color} = paramObj
        return new PolygonLayer({
            id: layerID,
            data: data,
            pickable: false,
            stroked: false,
            filled: true,
            wireframe: true,
            lineWidthMinPixels: 1,
            getPolygon: d => d.contour,
            getFillColor: color,
            getLineColor: [80, 80, 80],
            getLineWidth: 0,
            opacity: 1
        })
    }

    getRingLayer = (paramObj) => {
        let {data, layerID, color} = paramObj
        return new PolygonLayer({
            id: layerID,
            data: data,
            pickable: true,
            stroked: true,
            filled: true,
            wireframe: true,
            lineWidthMinPixels: 1,
            getPolygon: d => d.contour,
            //getElevation: d => d.population / d.area / 10,
            getFillColor: [255, 255, 0],
            getLineColor: [255, 255, 0],
            getLineWidth: 1,
            opacity: 1
        })
    }

    //
    showInfo = (event) => {
        let info = event.object.info
        info.replace("->", "<-->")
        alert(String(info))
    }

    filter2D = (data) => {//2D
        let newData = []
        data.forEach(line => {
            let path = line.path
            if (path[0][2] !== path[path.length - 1][2]) {
                newData.push(line)
            }
        })
        return newData
    }

    preState = false//
    highlightPoint_Edge = (info) => {
        let netflow = this.buttonState.netflow
        if(netflow.exist)return null
        if (info.picked && !this.preState) {
            this.preState = true
            let scatterInfo = info.object.info
            this.updateHighlightPoint_Edge(scatterInfo)
        }
        if (!info.picked && this.preState) {
            this.preState = false
            this.cancelHighlight()
        }
    }

    updateHighlightPoint_Edge(scatterInfo, from) {
        const { DC, EB } = this.results
        let dataKind = this.buttonState.dataKind.traffic ? "traffic" : "bd"
        let plane = !this.buttonState.plane.A ? (!this.buttonState.plane.B ? "" : "B") : (!this.buttonState.plane.B ? "A" : "AB")
        let edgeKind = this.buttonState.edgeKind.EB ? "EB" : "DC"
        let myData = edgeKind === "EB" ? EB : DC
        if (plane === "") { return true }
        let data = myData[plane + "_" + dataKind]
        if(data === undefined){console.log("data",plane + "_" + dataKind)}
        let highlightEdges = []
        let highlightScatter = []
        let ScatterObj = {}
        data.forEach(line=>{
            let info = line.info
            if(info.indexOf(scatterInfo)>-1){
                let {info, path, value, width} = line
                highlightEdges.push({color:[255,0,0], info, path, value, width})
                let cityArr = info.split(" ")[0].split("->")
                let source = cityArr[0], target = cityArr[1]
                ScatterObj[source] = true
                ScatterObj[target] = true
            }
        })
        
        Topnav.scatterData[dataKind].forEach(line=>{
            if(ScatterObj[line.info]===true){
                let {info, radius, value} = line
                let position = [...line.position]
                let fillColor = [255,105,180]
                if(line.info===scatterInfo)fillColor = [0, 255, 0]
                highlightScatter.push({fillColor, info, position, radius, value})
            }
        })
        let pathLayerParamObj = {data: highlightEdges, layerID: "highlightLayer", opacity: 0.5, filter2D: false}
        let pathLayer = this.getPathLayer(pathLayerParamObj)
        Map.allLayers["highlightPahtLayers"] = [pathLayer]
        
        let scatterLayerParamObj ={data: highlightScatter, layerID: "highlightScatter"}
        let scatterLayer = this.getScatterPlotLayer(scatterLayerParamObj)
        Map.allLayers["highlightScatterLayers"] = [scatterLayer]
        this.combineLayers()
    }

    updateHighlightEdge=(edgeInfo)=>{
        const { DC, EB } = this.results
        let dataKind = this.buttonState.dataKind.traffic ? "traffic" : "bd"
        let plane = !this.buttonState.plane.A ? (!this.buttonState.plane.B ? "" : "B") : (!this.buttonState.plane.B ? "A" : "AB")
        let edgeKind = this.buttonState.edgeKind.EB ? "EB" : "DC"
        let myData = edgeKind === "EB" ? EB : DC
        if (plane === "") { return true }
        let data = myData[plane + "_" + dataKind]
        if(data === undefined){console.log("data",plane + "_" + dataKind)}
        let highlightEdges = []
        let highlightScatter = []
        let ScatterObj = {}
        data.forEach(line=>{
            let info = line.info
            if(info.indexOf(edgeInfo)>-1){
                let {info, path, value, width} = line
                highlightEdges.push({color:[255,0,0], info, path, value, width})
                let cityArr = info.split(" ")[0].split("->")
                let source = cityArr[0], target = cityArr[1]
                ScatterObj[source] = true
                ScatterObj[target] = true
            }
        })

        Topnav.scatterData[dataKind].forEach(line=>{
            if(ScatterObj[line.info]===true){
                let {info, radius, value} = line
                let position = [...line.position]
                let fillColor = [0, 255, 0]
                highlightScatter.push({fillColor, info, position, radius, value})
            }
        })
        let pathLayerParamObj = {data: highlightEdges, layerID: "highlightLayer", opacity: 0.5, filter2D: false}
        let pathLayer = this.getPathLayer(pathLayerParamObj)
        Map.allLayers["highlightPahtLayers"] = [pathLayer]
        
        let scatterLayerParamObj ={data: highlightScatter, layerID: "highlightScatter"}
        let scatterLayer = this.getScatterPlotLayer(scatterLayerParamObj)
        Map.allLayers["highlightScatterLayers"] = [scatterLayer]
        this.combineLayers()
    }

    cancelHighlight=()=>{
        Map.allLayers["highlightPahtLayers"] = []
        Map.allLayers["highlightScatterLayers"] = []
        this.combineLayers()
    }

    combineLayers = ()=>{
        let layers = []
        //pathLayernetflowLayerringLayer
        layers=[...Map.allLayers.pathLayers, ...Map.allLayers.netflowLayers, ...Map.allLayers.ringLayers, ...Map.allLayers.scatterLayers]
        //highlightLayer
        for(let attr in Map.allLayers){
            if(attr.indexOf("highlight")>-1){
                layers = [...layers, ...Map.allLayers[attr]]
            }
        }
        //rightClickLayer
        for(let attr in this.rightClickLayers){
            if(this.rightClickLayers[attr] !== null){
                layers.push(this.rightClickLayers[attr])
            }
        }
        layers = [...layers, ...Map.allLayers["textLayers"], ...Map.allLayers["polygonLayers"]]
        this.changeMapLayers(layers)        
    }

    rightClickScatter = {}
    rightClickLayers = {}
    clickScatter = (d,event)=>{
        if(event.leftButton){
            alert(d.object.info + " " + String(d.object.value))
        }else{
            let scatterID = d.object.info
            if(this.rightClickScatter[scatterID] === undefined) this.rightClickScatter[scatterID] = false
            this.rightClickScatter[scatterID] = !this.rightClickScatter[scatterID]//falsetrue
            if(!this.rightClickScatter[scatterID]){
                this.rightClickLayers[scatterID] = null
                this.combineLayers()
                return true
            }
            let edgeKind = this.buttonState.edgeKind.EB ? "EB" : "DC"
            if(this.buttonState.edgeKind.EB && this.buttonState.edgeKind.DC)return null
            const {DC, EB} = this.results;
            let dataKind = this.buttonState.dataKind.traffic ? "traffic" : "bd"
            let plane = !this.buttonState.plane.A ? (!this.buttonState.plane.B ? "" : "B") : (!this.buttonState.plane.B ? "A" : "AB")
            if (plane === "") { return true }
            let myData = edgeKind === "EB" ? DC : EB
            let data = myData[plane + "_" + dataKind]
            let edgesData = []
            data.forEach(line=>{
                let {info} = line
                if(info.indexOf(scatterID)>-1)edgesData.push(line)
            })
            let pathLayerParamObj = {data: edgesData, layerID: "rightClick"+String(scatterID)}
            let layer = this.getPathLayer(pathLayerParamObj)
            this.rightClickLayers[scatterID] = layer
            this.combineLayers()
        }
    }









}