import React, { Component } from 'react'
import "./index.css"
import PubSub from "pubsub-js"
import Map from './map/index'
export default class LeftColumn extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isFinish: false
        }
    }

    receiveDataInfo = () => {
        PubSub.subscribe('data', (_, stateObj) => {
            let { isFinish } = stateObj
            this.setState({ isFinish })
        });
    }
    componentDidMount() {
        //data
        this.receiveDataInfo()
    }
    render() {
        let { isFinish } = this.state
        return (
            <div className="leftcolumn">
                <div className="card">
                    <div className="map" style={{ height: "1200px"}}>
                        {
                            !isFinish ?
                                <h2>, ...</h2>
                                :
                                <Map />
                        }
                    </div>
                </div>
            </div>
        )
    }
}