import { path } from "@loaders.gl/loader-utils";
import china from "../data/china.json"
import coordinatesData from "../data/coordinates.json"
import rawData from "../data/rawData.json"
import ABlevel from "./ABlevel";
import KernelEdgeBundling from "./EdgeBundling";
import * as d3 from "d3"

for (let attr in coordinatesData) {
    coordinatesData[attr].x = coordinatesData[attr].x * 3
    coordinatesData[attr].y = coordinatesData[attr].y * 3
}


export default class ComputeData {
    static typeHeight = { "A_N": 70, "A_C": 0, "M": 120, "B_C": 170, "B_N": 240 }
    constructor() {
        this.ColorEB = [d3.rgb(0, 255, 128), d3.rgb(0, 0, 255)]
        this.ColorDC = [d3.rgb(255, 255, 0), d3.rgb(0, 0, 0)]

        this.typeHeight = ComputeData.typeHeight
        this.isSkipZero = true
        this.isSkipBusinessCore = true
        this.isCheckPathNegative = true
        this.isMerge_FT_Edeges = true

        this.china = china;
        this.coordinatesData = coordinatesData

        this.typeLine = {
            "A_C": [],//A , {from_city, from_coordinates, from_dev, from_level, from_port, to_city, to_coordinates, to_dev, to_level, to_port, traffic, bdUtilization}
            "A_N": [],//A 
            "A_CN": [],//A 
            "B_C": [],//B 
            "B_N": [],//B 
            "B_CN": [],//B 
            "AB": [],//AB
            "M": []//ABAB
        }
        this.allNodes = {} //:{"1129":{city, dev, coordinates, traffic, bdU, type},  ,,,,,}
        let data = this.processRawData(rawData)
        this.generateTypeData(data) //allNodestypeLine

    }

    getWidth(k,value,maxValue){
        return (k/30*value/maxValue)*3+0.1 >3 ? 3 : (k*value/maxValue)*3+0.1
    }

    getResults() {
        let EBResults = this.computeEBResults()
        let DCResults = this.computeDCResults()

        for (let attr in EBResults) {
            let maxValue = (attr.split("_b").length > 1) ? this.maxBdUtilization : this.maxTraffic
            let k = (attr.split("_b").length > 1) ? 500 : 300
            let valueType = (attr.split("_b").length > 1) ? "bandwidth utilization" : "traffic"
            for (let i = 0; i < EBResults[attr].length; i++) {
                let path = EBResults[attr][i]
                let value = path[0].value
                let color = this.setColor(value, maxValue, k, this.ColorEB[0], this.ColorEB[1] )
                let width = this.getWidth(k,value,maxValue)//(k*value/maxValue)*3+0.5 >3 ? 4 : (k*value/maxValue)*3+0.5
                let info = path[0].id + "->" + path[path.length - 1].id + " " + valueType + ":" + value
                let np = []
                for (let j = 0; j < path.length; j++) {
                    let line = path[j]
                    let x = line.x, y = line.y, z = line.z
                    if (this.isCheckPathNegative) {
                        x = x > 0 ? x : 0
                        y = y > 0 ? y : 0
                        z = z > 0 ? z : 0
                    }
                    np[j] = [x, y, z]
                }
                EBResults[attr][i] = { path: np, value, color, info, width }
            }
        }

        for (let attr in DCResults) {
            let maxValue = (attr.split("_b").length > 1) ? this.maxBdUtilization : this.maxTraffic
            let k = (attr.split("_b").length > 1) ? 500 : 300
            let valueType = (attr.split("_b").length > 1) ? "bandwidth utilization" : "traffic"
            for (let i = 0; i < DCResults[attr].length; i++) {
                let path = DCResults[attr][i]
                let value = path[0].value
                let np = []
                for (let j = 0; j < path.length; j++) {
                    let line = path[j]
                    np[j] = [line.x, line.y, line.z]
                }
                let color = this.setColor(value, maxValue, k, this.ColorDC[0], this.ColorDC[1])
                let width = this.getWidth(k,value,maxValue)//(k*value/maxValue)*3+0.5 >3 ? 4 : (k*value/maxValue)*3+0.5
                let info = path[0].id + "->" + path[path.length - 1].id + " " + valueType + ":" + value
                DCResults[attr][i] = { path: np, value, color, info, width }
            }
        }
        let chinaA = [this.get_China_Data(this.typeHeight["A_N"]), this.get_China_Data(this.typeHeight["A_C"])]
        let chinaB = [this.get_China_Data(this.typeHeight["B_N"]), this.get_China_Data(this.typeHeight["B_C"])]
        let allNodes = this.allNodes
        return { "DC": DCResults, "EB": EBResults, "China": { A: chinaA, B: chinaB }, allNodes }
    }

    processRawData(data) {
        data.forEach(line => {//
            line.bdUtilization = line.traffic / line.bandwidth
        })

        data = this.merge_Same_Edges(data) // a->b  a->b 

        if (this.isMerge_FT_Edeges === true) data = this.merge_FT_Edges(data) //a->b  b->a 

        data.forEach(line=>{//
            line.bdUtilization = line.bdUtilization / line.frequency
        })

        this.converSourceTarget(data)

        let maxTraffic = 0, maxBdUtilization = 0;
        //, , ,
        data.forEach(line => {
            //
            let { from_city, to_city } = line
            let from_coordinates = coordinatesData[from_city]
            let to_coordinates = coordinatesData[to_city]
            if (from_coordinates === undefined) console.log("", from_city)
            if (to_coordinates === undefined) console.log("", to_city)
            line.from_coordinates = from_coordinates
            line.to_coordinates = to_coordinates
            //
            line.bdUtilization = line.bdUtilization / line.frequency
            line.frequency = 1
            //    
            if (this.trafficLimit !== undefined) {
                if (line.traffic > this.trafficLimit) line.traffic = this.trafficLimit
            }
            if (this.bandwidthLimit !== undefined) {
                if (line.bdUtilization > this.bandwidthLimit) line.bdUtilization = this.bandwidthLimit
            }
            //   
            if (maxTraffic < line.traffic) maxTraffic = line.traffic
            if (maxBdUtilization < line.bdUtilization) maxBdUtilization = line.bdUtilization
        })

        this.maxTraffic = maxTraffic
        this.maxBdUtilization = maxBdUtilization
        return data
    }

    get_China_Data(height) {
        let features = this.china.features
        let withHeightChinaPath = []
        features.forEach(fea => {
            let coordinates = fea.geometry.coordinates
            coordinates.forEach(geo => {
                geo.forEach(path => {
                    let newPath = []
                    path.forEach(obj => {
                        if (height !== undefined) newPath.push([...obj, height])//3Dpath
                        else newPath.push([...obj])//2Dpath
                    })
                    withHeightChinaPath.push({ path: newPath })
                })
            })

        })
        return withHeightChinaPath
    }

    generateTypeData(data) {// typeline  typenodes
        let allNodes = {}
        let sumObj = {}

        data.forEach(line => {
            let { from_city, from_coordinates, from_dev, from_level, from_port, to_city, to_coordinates, to_dev, to_level, to_port, traffic, bdUtilization } = line
            //typeLine
            let flStr = from_level === "" ? "C" : "N";
            let tlStr = to_level === "" ? "C" : "N";
            let fromPlane = ABlevel.getLevel(from_dev, from_city)
            let toPlane = ABlevel.getLevel(to_dev, to_city)
            let planeStr = (fromPlane === toPlane ? fromPlane : ((fromPlane === "M" || toPlane === "M") ? "M" : "AB"))
            let levelStr = flStr === tlStr ? flStr : "CN"
            let typeStr = planeStr === "M" ? "M" : (planeStr === "AB" ? "AB" : (planeStr + "_" + levelStr))
            this.typeLine[typeStr].push({ from_city, from_coordinates, from_dev, from_level, from_port, to_city, to_coordinates, to_dev, to_level, to_port, traffic, bdUtilization })
            //
            let f_cityDev = from_city + "," + from_dev
            let t_cityDev = to_city + "," + to_dev
            let f_nodeType = fromPlane === "M" ? "M" : (fromPlane + "_" + flStr)
            let t_nodeType = toPlane === "M" ? "M" : (toPlane + "_" + tlStr)
            if (allNodes[f_cityDev] === undefined) {
                sumObj[f_cityDev] = 1
                let { x, y } = from_coordinates
                let z = this.typeHeight[f_nodeType]
                allNodes[f_cityDev] = { city: from_city, dev: from_dev, coordinates: { x, y, z }, traffic, bdUtilization, type: f_nodeType }
            } else {
                allNodes[f_cityDev].traffic = allNodes[f_cityDev].traffic + traffic
                allNodes[f_cityDev].bdUtilization = allNodes[f_cityDev].bdUtilization + bdUtilization
                sumObj[f_cityDev] = sumObj[f_cityDev] + 1
            }
            if (allNodes[t_cityDev] === undefined) {
                sumObj[t_cityDev] = 1
                let { x, y } = to_coordinates
                let z = this.typeHeight[t_nodeType]
                allNodes[t_cityDev] = { city: to_city, dev: to_dev, coordinates: { x, y, z }, traffic, bdUtilization, type: t_nodeType }
            } else {
                allNodes[t_cityDev].traffic = allNodes[t_cityDev].traffic + traffic
                allNodes[t_cityDev].bdUtilization = allNodes[t_cityDev].bdUtilization + bdUtilization
                sumObj[t_cityDev] = sumObj[t_cityDev] + 1
            }
        })
        //
        for (let attr in sumObj) {
            allNodes[attr].bdUtilization = allNodes[attr].bdUtilization / sumObj[attr]
        }

        this.allNodes = allNodes
    }

    computeEBResults() {
        let eb_2D = ["A_C", "A_N", "B_C", "B_N"]
        let eb_3D = ["A_CN", "B_CN"]
        let results = {}
        let FINAL = {}
        //2D
        eb_2D.forEach(attr => {
            let data = this.typeLine[attr]
            let nodes = {}
            let links_traffic = []
            let links_bd = []
            data.forEach(line => {
                let { from_city, from_dev, from_coordinates, to_city, to_coordinates, to_dev, traffic, bdUtilization } = line
                let fx = from_coordinates.x, fy = from_coordinates.y
                let tx = to_coordinates.x, ty = to_coordinates.y
                let source = from_city + "," + from_dev
                let target = to_city + "," + to_dev
                nodes[source] = { x: fx, y: fy }
                nodes[target] = { x: tx, y: ty }
                links_traffic.push({ source, target, value: traffic })
                links_bd.push({ source, target, value: bdUtilization })
            })
            let rType1 = attr + "_traffic"
            let rType2 = attr + "_bd"
            let r1 = KernelEdgeBundling.KernelEdgeBundling2D().nodes(nodes).edges(links_traffic)();
            let r2 = KernelEdgeBundling.KernelEdgeBundling2D().nodes(nodes).edges(links_bd)();
            this.convert2D(r1, attr)
            this.convert2D(r2, attr)
            results[rType1] = r1
            results[rType2] = r2

        })

        //3D
        eb_3D.forEach(attr => {
            let data = this.typeLine[attr]
            let nodes = {}
            let links_traffic = []
            let links_bd = []
            data.forEach(line => {
                let { from_city, from_dev, from_coordinates, to_city, to_coordinates, to_dev, traffic, bdUtilization } = line
                let fx = from_coordinates.x, fy = from_coordinates.y
                let tx = to_coordinates.x, ty = to_coordinates.y
                let source = from_city + "," + from_dev
                let target = to_city + "," + to_dev
                let ftype = this.allNodes[source].type
                let ttype = this.allNodes[target].type
                let fz = this.typeHeight[ftype]
                let tz = this.typeHeight[ttype]

                nodes[source] = { x: fx, y: fy, z: fz }
                nodes[target] = { x: tx, y: ty, z: tz }
                links_traffic.push({ source, target, value: traffic })
                links_bd.push({ source, target, value: bdUtilization })
            })
            let rType1 = attr + "_traffic"
            let rType2 = attr + "_bd"
            // let r1 = KernelEdgeBundling.KernelEdgeBundling3D_addSpring().nodes(nodes).edges(links_traffic)();
            // let r2 = KernelEdgeBundling.KernelEdgeBundling3D_addSpring().nodes(nodes).edges(links_bd)();

            // let r1 = KernelEdgeBundling.KernelEdgeBundling3D_addLength().nodes(nodes).edges(links_traffic)();
            // let r2 = KernelEdgeBundling.KernelEdgeBundling3D_addLength().nodes(nodes).edges(links_bd)();

            // let r1 = KernelEdgeBundling.KernelEdgeBundling3D().nodes(nodes).edges(links_traffic)();
            // let r2 = KernelEdgeBundling.KernelEdgeBundling3D().nodes(nodes).edges(links_bd)();
            // console.log("****************************KernelEdgeBundling3D*****************************")

            // let r1 = KernelEdgeBundling.KernelEdgeBundling3D_ADEB().nodes(nodes).edges(links_traffic)();
            // let r2 = KernelEdgeBundling.KernelEdgeBundling3D_ADEB().nodes(nodes).edges(links_bd)();
            // console.log("****************************KernelEdgeBundling3D_ADEB*****************************")

            let r1 = KernelEdgeBundling.KernelEdgeBundling3D_ADEB2().nodes(nodes).edges(links_traffic)();
            let r2 = KernelEdgeBundling.KernelEdgeBundling3D_ADEB2().nodes(nodes).edges(links_bd)();
            console.log("****************************KernelEdgeBundling3D_ADEB2*****************************")


            results[rType1] = r1
            results[rType2] = r2
        })

        let ABresult = { "AB_bd": [], "AB_traffic": [] }
        this.typeLine["AB"].forEach(line => {
            let { from_city, from_dev, from_coordinates, to_city, to_coordinates, to_dev, traffic, bdUtilization } = line
            let source = from_city + "," + from_dev
            let fx = from_coordinates.x, fy = from_coordinates.y, fz = this.typeHeight[this.allNodes[source].type]
            let target = to_city + "," + to_dev
            let tx = to_coordinates.x, ty = to_coordinates.y, tz = this.typeHeight[this.allNodes[target].type]
            if (from_city === to_city) ty = ty + 0.0001
            ABresult["AB_bd"].push([{ id: source, x: fx, y: fy, z: fz, value: bdUtilization }, { id: target, x: tx, y: ty, z: tz, value: bdUtilization }])
            ABresult["AB_traffic"].push([{ id: source, x: fx, y: fy, z: fz, value: traffic }, { id: target, x: tx, y: ty, z: tz, value: traffic }])
        })

        let Mresult = { "M_bd": [], "M_traffic": [] }
        this.typeLine["M"].forEach(line => {
            let { from_city, from_dev, from_coordinates, to_city, to_coordinates, to_dev, traffic, bdUtilization } = line
            let source = from_city + "," + from_dev
            let fx = from_coordinates.x, fy = from_coordinates.y, fz = this.typeHeight[this.allNodes[source].type]
            let target = to_city + "," + to_dev
            let tx = to_coordinates.x, ty = to_coordinates.y, tz = this.typeHeight[this.allNodes[target].type]
            Mresult["M_bd"].push([{ id: source, x: fx, y: fy, z: fz, value: bdUtilization }, { id: target, x: tx, y: ty, z: tz, value: bdUtilization }])
            Mresult["M_traffic"].push([{ id: source, x: fx, y: fy, z: fz, value: traffic }, { id: target, x: tx, y: ty, z: tz, value: traffic }])
        })


        FINAL["A_bd"] = [...results["A_C_bd"], ...results["A_N_bd"], ...results["A_CN_bd"]]
        FINAL["A_traffic"] = [...results["A_C_traffic"], ...results["A_N_traffic"], ...results["A_CN_traffic"]]

        FINAL["B_bd"] = [...results["B_C_bd"], ...results["B_N_bd"], ...results["B_CN_bd"]]
        FINAL["B_traffic"] = [...results["B_C_traffic"], ...results["B_N_traffic"], ...results["B_CN_traffic"]]

        FINAL["AB_bd"] = [...Mresult["M_bd"], ...ABresult["AB_bd"], ...FINAL["A_bd"], ...FINAL["B_bd"]]
        FINAL["AB_traffic"] = [...Mresult["M_traffic"], ...ABresult["AB_traffic"], ...FINAL["A_traffic"], ...FINAL["B_traffic"]]

        return FINAL
    }

    computeDCResults() {
        let planeA = ["A_C", "A_N", "A_CN"]
        let planeB = ["B_C", "B_N", "B_CN"]
        let results = { "A_traffic": [], "A_bd": [], "B_traffic": [], "B_bd": [], "AB_traffic": [], "AB_bd": [] }
        for (let attr in this.typeLine) {
            this.typeLine[attr].forEach(line => {
                let { from_city, from_dev, from_coordinates, to_city, to_coordinates, to_dev, traffic, bdUtilization } = line
                let source = from_city + "," + from_dev
                let fx = from_coordinates.x, fy = from_coordinates.y, fz = this.typeHeight[this.allNodes[source].type]
                let target = to_city + "," + to_dev
                let tx = to_coordinates.x, ty = to_coordinates.y, tz = this.typeHeight[this.allNodes[target].type]

                if (planeA.indexOf(attr) > -1) {
                    results["A_traffic"].push([{ id: source, x: fx, y: fy, z: fz, value: traffic }, { id: target, x: tx, y: ty, z: tz, value: traffic }])
                    results["A_bd"].push([{ id: source, x: fx, y: fy, z: fz, value: bdUtilization }, { id: target, x: tx, y: ty, z: tz, value: bdUtilization }])
                } else if (planeB.indexOf(attr) > -1) {
                    results["B_traffic"].push([{ id: source, x: fx, y: fy, z: fz, value: traffic }, { id: target, x: tx, y: ty, z: tz, value: traffic }])
                    results["B_bd"].push([{ id: source, x: fx, y: fy, z: fz, value: bdUtilization }, { id: target, x: tx, y: ty, z: tz, value: bdUtilization }])
                } else {
                    if (from_city === to_city) ty = ty + 0.0001
                    results["AB_traffic"].push([{ id: source, x: fx, y: fy, z: fz, value: traffic }, { id: target, x: tx, y: ty, z: tz, value: traffic }])
                    results["AB_bd"].push([{ id: source, x: fx, y: fy, z: fz, value: bdUtilization }, { id: target, x: tx, y: ty, z: tz, value: bdUtilization }])
                }
            })
        }
        results["AB_traffic"] = [...results["AB_traffic"], ...results["A_traffic"], ...results["B_traffic"]]
        results["AB_bd"] = [...results["AB_bd"], ...results["A_bd"], ...results["B_bd"]]
        return results
    }

    convert2D(data, type) {
        let z = this.typeHeight[type]
        data.forEach(path => {
            path.forEach(line => {
                line.z = z
            })
        })
    }

    setColor(value, maxValue, k, from, to) {
        let t = value / maxValue * k
        if (t > 1) t = 1
        from = from === undefined ? d3.rgb(0, 255, 128) : from
        to = to === undefined ? d3.rgb(0, 0, 255) : to
        let f2t = d3.interpolate(from, to)
        let rgbArr = f2t(t).replace("rgb(", "").replace(")", "").split(",")

        return [parseInt(rgbArr[0]), parseInt(rgbArr[1]), parseInt(rgbArr[2])]
    }

    saveTypeNodes(nodeObj, sumObj, city, dev, traffic, bandwidth) {
        let nodeStr = city + dev
        if (nodeObj[nodeStr] === undefined) {
            nodeObj[nodeStr] = { name: city, dev, traffic, bandwidth }
            sumObj[nodeStr] = 1
        } else {
            nodeObj[nodeStr].traffic = nodeObj[nodeStr].traffic + traffic
            nodeObj[nodeStr].bandwidth = nodeObj[nodeStr].bandwidth + bandwidth
            sumObj[nodeStr] = sumObj[nodeStr] + 1
        }
    }

    setMaxTrafficLimit(limit) {
        this.trafficLimit = limit
        return this
    }
    setMaxBandwidthLimit(limit) {
        this.bandwidthLimit = limit
        return this
    }
    setLevelScale(levelHeight) {//{"":0}
        this.levelHeight = levelHeight
        return this
    }

    isCheckPathNegative(isCheckPathNegative) {
        this.isCheckPathNegative = isCheckPathNegative === undefined ? true : isCheckPathNegative
    }

    isMerge_FT_Edges(isMerge_FT_Edeges) {
        this.isMerge_FT_Edeges = (isMerge_FT_Edeges !== undefined ? isMerge_FT_Edeges : true)// a->b  b->a , 
        return this
    }
    isSkipZero(isSkipZero) {
        this.isSkipZero = isSkipZero === undefined ? true : isSkipZero
    }
    isSkipBusinessCore(isSkipBusinessCore) {
        this.isSkipBusinessCore = isSkipBusinessCore === undefined ? true : isSkipBusinessCore
    }

    merge_Same_Edges(data) {// a->b  a->b 
        let indexObj = {}
        let mergesData = []
        for (let i = 0; i < data.length; i++) {
            let { from_city, from_dev, from_level, from_port, to_city, to_dev, to_level, to_port, traffic, bandwidth, bdUtilization } = data[i]
            traffic = +traffic
            bandwidth = +bandwidth
            //
            if (isNaN(traffic) || isNaN(bandwidth)) continue
            if (this.isSkipZero && (traffic === 0 || bandwidth === 0)) continue
            if (this.isSkipBusinessCore && (from_level === "" || to_level === "")) continue

            let fromObjStr = from_city + from_dev, toObjStr = to_city + to_dev;
            let edgeStr = fromObjStr + toObjStr
            if (indexObj[edgeStr] === undefined) {
                indexObj[edgeStr] = mergesData.length
                mergesData.push({ from_city, from_dev, from_level, from_port, to_city, to_dev, to_level, to_port, traffic, bandwidth, bdUtilization, frequency: 1 })
            } else {
                let index = indexObj[edgeStr]

                mergesData[index].traffic = mergesData[index].traffic + traffic
                mergesData[index].bdUtilization = mergesData[index].bdUtilization + bdUtilization
                mergesData[index].frequency = mergesData[index].frequency + 1
            }
        }

        return mergesData
    }

    merge_FT_Edges(data) {// a->b  b->a 
        let mergeFTData = []
        let indexObj_FT = {}
        for (let i = 0; i < data.length; i++) {
            let { from_city, from_dev, from_level, from_port, to_city, to_dev, to_level, to_port, traffic, bandwidth, frequency, bdUtilization } = data[i]
            let fromStr = from_city + from_dev
            let toStr = to_city + to_dev
            let FT = fromStr+toStr
            let TF = toStr+fromStr
            if (indexObj_FT[FT] === undefined) {
                indexObj_FT[FT] = indexObj_FT[TF] = mergeFTData.length
                mergeFTData.push({ from_city, from_dev, from_level, from_port, to_city, to_dev, to_level, to_port, traffic, bandwidth, frequency, bdUtilization })
            } else {
                let index = indexObj_FT[FT]
                mergeFTData[index].traffic = mergeFTData[index].traffic + traffic
                mergeFTData[index].bdUtilization = mergeFTData[index].bdUtilization + bdUtilization
                mergeFTData[index].frequency = mergeFTData[index].frequency + frequency
            }
        }
        return mergeFTData
    }


    getProgressInfo(setMapProgress) {
        this.setMapProgress = setMapProgress;
    }

    converSourceTarget(data){//
        data.forEach(line=>{
            let { from_city, from_dev, from_level, from_port, to_level}  = line
            if(from_level === "" && to_level===""){
                let temp_city = from_city,
                    temp_dev = from_dev,
                    temp_level = from_level,
                    temp_port = from_port

                line.from_city = line.to_city
                line.to_city = temp_city

                line.from_dev = line.to_dev
                line.to_dev = temp_dev

                line.from_level = line.to_level
                line.to_level = temp_level
                
                line.from_port = line.to_port
                line.to_port = temp_port
            }
        })
    }
}