import React, { Component } from 'react'
import "./index.css"
import PubSub from "pubsub-js"
import Layer from '../row/leftColumn/map/Layer'
import Map from '../row/leftColumn/map'
import ComputeData from '../row/leftColumn/map/ComputeData'

export default class Topnav extends Component {
    static scatterData = { bd: [], traffic: [] }
    constructor(props) {
        super(props)
        this.state = {
            dataKind: {
                traffic: true,
                bd: false
            },
            edgeKind: {
                EB: false,
                DC: true
            },
            plane: {
                A: true,
                B: true
            },
            netflow: { exist: false }
        }
        //data
        this.receiveDataInfo()
        //Map
        this.receiveChangeMapLayers()
        //button
        //this.sendButtonStateInfo()
        Topnav.state = this.state
        //map
        this.receiveMapDidMount()
        this.receiveRightInfo()
        this.layer = new Layer()

    }

    receiveRightInfo = () => {
        PubSub.subscribe('rightDataKind', (_, stateObj) => {
            let { setDataKind } = stateObj
            this.setRightColumnDataKind = setDataKind
        });
    }

    receiveChangeMapLayers = () => {
        PubSub.subscribe('changeMapLayers', (_, stateObj) => {
            let { changeMapLayers } = stateObj
            this.changeMapLayers = changeMapLayers
        });
    }

    receiveDataInfo = () => {
        PubSub.subscribe('data', (_, stateObj) => {// {dataKind, edgeKind, plane, netflow }
            let { results, netflowResults } = stateObj
            this.results = results
            this.netflowResults = netflowResults
            //nodetrafficbd
            let allNodes = results.allNodes
            let maxNodeTraffic = 0, maxNodeBd = 0
            for (let attr in allNodes) {
                let node = allNodes[attr]
                let { traffic, bdUtilization } = node
                if (traffic > maxNodeTraffic) maxNodeTraffic = traffic
                if (bdUtilization > maxNodeBd) maxNodeBd = bdUtilization
            }
            this.maxNodeBd = maxNodeBd
            this.maxNodeTraffic = maxNodeTraffic
        });
    }

    receiveMapDidMount = () => {
        PubSub.subscribe('MapDidMount', (_, stateObj) => {// {dataKind, edgeKind, plane, netflow }
            let { DidMount } = stateObj
            if (DidMount) {
                this.computeLayers()
                this.combineLayers()
            }
        });
    }



    getButtonColor = (id) => {
        let highlightColor = "#44ffff"
        let deHighlightColor = "#ffffff"
        let { dataKind, edgeKind, plane, netflow } = this.state
        let color
        if (id === "traffic" || id === "bd") {
            color = dataKind[id] ? highlightColor : deHighlightColor

        } else if (id === "EB" || id === "DC") {
            color = edgeKind[id] ? highlightColor : deHighlightColor
        } else if (id === "A" || id === "B") {
            color = plane[id] ? highlightColor : deHighlightColor
        } else {//netflow
            color = netflow.exist ? highlightColor : deHighlightColor
        }
        return color
    }

    updateButtonState = (id) => {
        return () => {
            let { dataKind, edgeKind, plane, netflow } = this.state
            if (id === "traffic" || id === "bd") {
                let other = id === "traffic" ? "bd" : "traffic"
                dataKind[other] = !dataKind[other]
                dataKind[id] = !dataKind[id]
                this.setState({ dataKind })
            } else if (id === "EB" || id === "DC") {
                edgeKind[id] = !edgeKind[id]
                this.setState({ edgeKind })
            } else if (id === "A" || id === "B") {
                plane[id] = !plane[id]
                this.setState({ plane })
            } else {//netflow
                netflow.exist = !netflow.exist
                this.setState({ netflow })
            }

        }
    }

    computeLayers = () => {
        //
        Map.allLayers = {}
        Topnav.scatterData = { bd: [], traffic: [] }
        Map.currentEdgeData = []
        Map.currentScatterData = []

        let { dataKind, edgeKind, plane, netflow } = this.state

        Map.currentDataKind = dataKind

        //
        let edgeKindArr = []
        if (edgeKind.EB) edgeKindArr.push("EB")
        if (edgeKind.DC) edgeKindArr.push("DC")
        //
        if (edgeKindArr.length === 0) return true

        //dataKind
        let currentDataKind = dataKind.traffic ? "traffic" : "bd"

        let currentPlane = plane.A ? (plane.B ? "AB" : "A") : (plane.B ? "B" : "")

        Topnav.currentPlane = currentPlane

        //0. 
        this.scatterSet = new Set()

        //1.  pathLayer
        let pathLayers = []
        Map.allLayers["pathLayers"] = pathLayers
        if (currentPlane === "") {//
            //pathLayers
        }
        else {
            edgeKindArr.forEach(edgeKind => {//
                let edgeKindData = this.results[edgeKind]
                let attr = currentPlane + "_" + currentDataKind
                let data = edgeKindData[attr]
                Map.currentEdgeData = [...Map.currentEdgeData, data]
                let layerID = edgeKind + "_" + attr
                let pathLayerParamObj = { data, layerID }
                let pathLayer = this.layer.getPathLayer(pathLayerParamObj)
                pathLayers.push(pathLayer)
                //pathLayers
                this.computeScatterData(data)
            });
        }

        //2.  netflowLayers
        if(netflow.exist){
            Topnav.netflowExist = true
        }else{
            Topnav.netflowExist = false
        }
        let netflowLayers = []
        Map.allLayers["netflowLayers"] = netflowLayers
        if (netflow.exist) {
            //EBpathlayer
            if (edgeKind.EB) {
                let data = this.netflowResults.edge["EB"]
                let layerID = "netflow_EB"
                let pathLayerParamObj = { data, layerID }
                netflowLayers.push(this.layer.getPathLayer(pathLayerParamObj))
            }
            //DCpathlayer
            if (edgeKind.DC) {
                let data = this.netflowResults.edge["DC"]
                let layerID = "netflow_DC"
                let pathLayerParamObj = { data, layerID }
                netflowLayers.push(this.layer.getPathLayer(pathLayerParamObj))
            }
            //netflow
            let nodes = this.netflowResults.nodes
            for (let attr in nodes) {
                if (!this.scatterSet.has(attr)) {
                    let position = nodes[attr].coordinates
                    let value = nodes[attr].value
                    let radius = nodes[attr].radius
                    let fillColor = nodes[attr].fillColor
                    Topnav.scatterData[currentDataKind].push({ info: attr, position, value, radius, fillColor })
                }
            }
        }
        //3.netflowringLayer
        let ringLayers = []
        Map.allLayers["ringLayers"] = ringLayers
        let ringID = 0
        if (netflow.exist) {
            this.netflowResults["edge"]["Ring"].forEach(line => {//ringDataLayer
                let { city, color, coordinates, value } = line
                let radius = this.netflowResults["nodes"][city]["radius"] * 1.5
                let z = coordinates[2]
                let x0 = coordinates[0] - radius
                let y0 = coordinates[1] - radius

                let x1 = coordinates[0] + radius
                let y1 = coordinates[1] - radius

                let x2 = coordinates[0] + radius
                let y2 = coordinates[1] + radius

                let x3 = coordinates[0] - radius
                let y3 = coordinates[1] + radius

                let polygonData = [{ contour: [[x0, y0, z], [x1, y1, z], [x2, y2, z], [x3, y3, z], [x0, y0, z]] }]
                let data = polygonData
                let layerID = "ring" + ringID
                let paramObj = { data, layerID }
                ringLayers.push(this.layer.getRingLayer(paramObj))
                ringID++
            })
        }

        //4. ScatterLayer TextLayer
        Map.currentScatterData = [...Topnav.scatterData[currentDataKind]]//scatterData
        let scatterParamObj = { data: Topnav.scatterData[currentDataKind], layerID: "scatter-plot" }
        let scatterLayer = this.layer.getScatterPlotLayer(scatterParamObj)
        Map.allLayers["scatterLayers"] = [scatterLayer]
        let textParamObj = { data: Topnav.scatterData[currentDataKind], layerID: "text" }
        let textLayer = this.layer.getTextLayer(textParamObj)
        Map.allLayers["textLayers"] = [textLayer]

        //5.2D
        let polygonArr = []
        Map.allLayers["polygonLayers"] = polygonArr
        let minX = 1000000, maxX = 0
        let minY = 1000000, maxY = 0
        Topnav.scatterData[currentDataKind].forEach(line => {
            let x = line.position[0]
            let y = line.position[1]
            if (minX > x) minX = x
            if (maxX < x) maxX = x
            if (minY > y) minY = y
            if (maxY < y) maxY = y
        })
        let A0 = ComputeData.typeHeight["A_N"]
        let A1 = ComputeData.typeHeight["A_C"]
        let B0 = ComputeData.typeHeight["B_C"]
        let B1 = ComputeData.typeHeight["B_N"]
        let polygonData0 = [{ contour: [[minX, minY, A0], [minX, maxY, A0], [maxX, maxY, A0], [maxX, minY, A0], [minX, minY, A0]] }]
        let polygonData1 = [{ contour: [[minX, minY, A1], [minX, maxY, A1], [maxX, maxY, A1], [maxX, minY, A1], [minX, minY, A1]] }]
        let polygonData2 = [{ contour: [[minX, minY, B0], [minX, maxY, B0], [maxX, maxY, B0], [maxX, minY, B0], [minX, minY, B0]] }]
        let polygonData3 = [{ contour: [[minX, minY, B1], [minX, maxY, B1], [maxX, maxY, B1], [maxX, minY, B1], [minX, minY, B1]] }]
        const normalPlaneColor = [0, 0, 255, 10]
        const corePlaneColor = [255, 0, 0, 10]
        //  edgeKind DC  EB
        //1. pathLayerscurrentPlane A B  AB  ""
        //2. netflow netflowtrueA
        let hasA = false, hasB = false
        if (currentPlane.indexOf("A") > -1) hasA = true
        if (currentPlane.indexOf("B") > -1) hasB = true
        if (netflow.exist) hasA = true
        if (hasA) {
            let polygonLayerID0 = { data: polygonData0, layerID: "p0", color: normalPlaneColor }
            let polygonLayerID1 = { data: polygonData1, layerID: "p1", color: corePlaneColor }
            polygonArr.push(this.layer.getPolygonLayer(polygonLayerID0))
            polygonArr.push(this.layer.getPolygonLayer(polygonLayerID1))
        }
        if (hasB) {
            let polygonLayerID2 = { data: polygonData2, layerID: "p2", color: corePlaneColor }
            let polygonLayerID3 = { data: polygonData3, layerID: "p3", color: normalPlaneColor }
            polygonArr.push(this.layer.getPolygonLayer(polygonLayerID2))
            polygonArr.push(this.layer.getPolygonLayer(polygonLayerID3))
        }
        return true
    }

    combineLayers = () => {
        let layers = []
        //pathLayernetflowLayerringLayer
        if (Map.allLayers.pathLayers === undefined) Map.allLayers.pathLayers = []
        if (Map.allLayers.netflowLayers === undefined) Map.allLayers.netflowLayers = []
        if (Map.allLayers.ringLayers === undefined) Map.allLayers.ringLayers = []
        if (Map.allLayers.scatterLayers === undefined) Map.allLayers.scatterLayers = []
        if (Map.allLayers.textLayers === undefined) Map.allLayers.textLayers = []
        if (Map.allLayers.polygonLayers === undefined) Map.allLayers.polygonLayers = []


        layers = [...Map.allLayers.pathLayers,
        ...Map.allLayers.netflowLayers,
        ...Map.allLayers.ringLayers,
        ...Map.allLayers.scatterLayers,
        ...Map.allLayers["textLayers"],
        ...Map.allLayers["polygonLayers"],
        ]
        this.changeMapLayers(layers)
    }

    computeScatterData = (data) => {// computeData 
        let getScatterRadius = (value, kind) => {
            let maxRadius = 8
            let minRadius = 2
            if (kind === "traffic") {
                let t = value / this.maxNodeTraffic * 10
                t = t > 1 ? 1 : t
                let radius = parseInt((maxRadius - minRadius) * t + minRadius)
                return radius
            } else {
                let t = value / this.maxNodeBd * 10
                t = t > 1 ? 1 : t
                let radius = (maxRadius - minRadius) * t + minRadius
                return radius
            }
        }
        let { allNodes } = this.results
        data.forEach(line => {
            let infoArr = line.info.split(" ")[0].split("->")
            let source = infoArr[0]
            let target = infoArr[1]
            if (!this.scatterSet.has(source)) {
                this.scatterSet.add(source)
                let node = allNodes[source]
                let { x, y, z } = node.coordinates
                let { traffic, bdUtilization, type } = node
                let color = type.indexOf("C") > 0 ? [255, 0, 0] : [0, 0, 255]
                let r_t = getScatterRadius(traffic, "traffic")
                let r_b = getScatterRadius(bdUtilization, "bd")
                Topnav.scatterData.bd.push({ info: source, position: [x, y, z], value: bdUtilization, radius: r_b, fillColor: color })
                Topnav.scatterData.traffic.push({ info: source, position: [x, y, z], value: traffic, radius: r_t, fillColor: color })
            }
            if (!this.scatterSet.has(target)) {
                this.scatterSet.add(target)
                let node = allNodes[target]
                let { x, y, z } = node.coordinates
                let { traffic, bdUtilization, type } = node
                let color = type.indexOf("C") > 0 ? [255, 0, 0] : [0, 0, 255]
                let r_t = getScatterRadius(traffic, "traffic")
                let r_b = getScatterRadius(bdUtilization, "bd")
                Topnav.scatterData.bd.push({ info: target, position: [x, y, z], value: bdUtilization, radius: r_b, fillColor: color })
                Topnav.scatterData.traffic.push({ info: target, position: [x, y, z], value: traffic, radius: r_t, fillColor: color })
            }
        })

    }

    sendButtonStateInfo = () => {//
        let { dataKind, edgeKind, plane, netflow } = this.state
        PubSub.publish("button", { button: { dataKind, edgeKind, plane, netflow } })
    }

    componentDidUpdate() {
        let { dataKind } = this.state
        if (dataKind.traffic) this.setRightColumnDataKind({ dataKind: "" })
        if (dataKind.bd) this.setRightColumnDataKind({ dataKind: "" })
        //Maplayers
        this.computeLayers()
        this.combineLayers()

    }


    render() {
        return (
            <div className="topnav">
                <button onClick={this.updateButtonState("traffic")} style={{ backgroundColor: this.getButtonColor("traffic") }}></button>
                <button onClick={this.updateButtonState("bd")} style={{ backgroundColor: this.getButtonColor("bd") }}></button>
                <button onClick={this.updateButtonState("EB")} style={{ backgroundColor: this.getButtonColor("EB") }}></button>
                <button onClick={this.updateButtonState("DC")} style={{ backgroundColor: this.getButtonColor("DC") }}></button>
                <button onClick={this.updateButtonState("A")} style={{ backgroundColor: this.getButtonColor("A") }}>A</button>
                <button onClick={this.updateButtonState("B")} style={{ backgroundColor: this.getButtonColor("B") }}>B</button>
                <button onClick={this.updateButtonState("netflow")} style={{ backgroundColor: this.getButtonColor("netflow") }}>Netflow</button>
                {/* <button style={{ float: "right", marginRight: "2px" }}></button> */}
            </div>
        )
    }
}