import React, { Component } from 'react'
import DeckGL from '@deck.gl/react'
import { OrbitView } from "@deck.gl/core"
import PubSub from "pubsub-js"

export default class Map extends Component {
    static allLayers = {}
    static currentScatterData = {A:[], B:[]}
    static currentEdgeData = {A:[], B:[]}
    static currentDataKind = "traffic"
    constructor(props){
        super(props)
        this.state = {
            layers:[]
        }

        this.INITIAL_VIEW_STATE = {
            target: [965.7479834999999, 300.012138, 0],
            zoom: 1,
            rotationOrbit: 0,
            rotationX: 40,
            minRotationX: -90,
            maxRotationX: 90,
            minZoom: -10,
            maxZoom: 10
        }

        let changeMapLayers = this.changeMapLayers
        PubSub.publish("changeMapLayers", {changeMapLayers})
    }

    changeMapLayers = (layers)=>{
        this.setState({layers})
    }

    componentDidMount(){
        //MapTopnavbuttonStatelayers
        PubSub.publish("MapDidMount", {DidMount:true})
    }

    render(){
        const { layers } = this.state
        let style = {
            top:"125px",
            left:"50px",
            width:"70%",
            height:"120%",
        }
        return (
            <DeckGL
            views={new OrbitView({
                orbitAxis: 'Z',
            })}
            initialViewState={this.INITIAL_VIEW_STATE}
            controller={true}
            layers={[...layers]}
            style = {style}
            id="deckGL"
        />
        )
    }
}