import React, { Component } from 'react'
import PubSub from "pubsub-js"
import "./index.css"
import Map from '../leftColumn/map'
import Layer from '../leftColumn/map/Layer'
import provCityData from "../leftColumn/data/prov_city.json"
import Topnav from '../../topnav'
export default class RightColumn extends Component {

    nodeThreshold = Infinity
    edgeThreshold = Infinity
    planeDiffThreshold = Infinity

    static currentHover = ""

    nodeRef = React.createRef()
    edgeRef = React.createRef()
    doublePlaneRef = React.createRef()

    constructor(props) {
        super(props)
        this.state = {
            dataKind: "",
            myNodes: [],
            myEdges: [],
            myPlane: []
        }
        this.processProv2City()
    }

    componentDidMount() {
        let setDataKind = this.setDataKind
        PubSub.publish("rightDataKind", { setDataKind })

    }
    setDataKind = (dataKind) => {
        let dk = dataKind.dataKind
        this.setState({ dataKind: dk, myNodes: [], myEdges: [], myPlane: [] })
    }

    processProv2City = () => {
        let provData = provCityData[0]
        let cityData = provCityData[1]
        this.city2prov = {}
        cityData.forEach(city => {
            let cityName = city.name
            let provID = city.ProID
            for (let i = 0; i < provData.length; i++) {
                let id = provData[i].ProID
                let provName = provData[i].name
                if (provID === id) {
                    this.city2prov[cityName] = provName
                    break;
                }
            }
        })
        console.log("city2prov", this.city2prov)
    }

    updateTable = (kind, ref) => {
        return () => {
            let thresholdValue = ref.current.value
            if (isNaN(thresholdValue) || thresholdValue === "") {
                alert("")
                return false
            }
            if (kind === "node") {
                if(Topnav.netflowExist){
                    alert("netflow")
                    return false
                } 
                this.nodeThreshold = thresholdValue
                this.updateMyNodes()
            }
            if (kind === "edge") {
                if(Topnav.netflowExist){
                    alert("netflow")
                    return false
                } 
                this.edgeThreshold = thresholdValue
                this.updateMyEdges()
            }
            if (kind === "doublePlane") {
                if (Topnav.currentPlane !== "AB"){
                    alert("AB")
                    return false
                } 
                if(Topnav.netflowExist){
                    alert("netflow")
                    return false
                } 
                this.planeDiffThreshold = thresholdValue
                this.updateDoublePlane()

            }
        }
    }

    updateMyNodes = () => {
        let myNodes = []
        Map.currentScatterData.forEach(node => {
            let { info, value, position, radius, fillColor } = node
            if (value > this.nodeThreshold) {
                let city = info.split(",")[0]
                let dev = info.split(",")[1]
                myNodes.push({ ID: info, city, dev, value, position, radius, fillColor })
            }
        })
        myNodes.sort((a, b) => b.value - a.value)
        this.setState({ myNodes })
    }

    updateMyEdges = () => {
        let myEdges = []
        Map.currentEdgeData.forEach(edgeArr => {
            edgeArr.forEach(edge => {
                let { path, value, color, info, width } = edge
                if (value > this.edgeThreshold) {
                    info = info.split(" ")[0]
                    let source = info.split("->")[0]
                    let target = info.split("->")[1]
                    myEdges.push({ ID: info, source, target, value, path, color, width })
                }
            })
        })
        myEdges.sort((a, b) => b.value - a.value)
        this.setState({ myEdges })
    }
    updateDoublePlane = () => {
        let myPlane = []

        //,,datakind
        this.prov2city = {}
        Map.currentScatterData.forEach(node => {
            let { info, value } = node
            let city = info.split(",")[0]
            let dev = info.split(",")[1]
            let ID = city + "," + dev
            for (let attr in this.city2prov) {
                if (attr.indexOf(city) > -1) {
                    let prov = this.city2prov[attr]
                    if (this.prov2city[prov] === undefined) this.prov2city[prov] = []
                    this.prov2city[prov].push({ ID, prov, city, dev, value })
                    break;
                }
            }
        })

        //value
        for (let attr in this.prov2city) {
            let sumValue = 0
            this.prov2city[attr].forEach(line => {
                sumValue = sumValue + line.value
            })
            let max = 0, min = Infinity
            this.prov2city[attr].forEach(line => {
                let percentage = line.value / sumValue
                if (percentage > max) max = percentage
                if (percentage < min) min = percentage
            })
            if ((max - min) > this.planeDiffThreshold) {
                this.prov2city[attr].forEach(line => {
                    line.diff = (max - min)
                    myPlane.push(line)
                })
            }
        }
        //diff
        myPlane.sort((a, b) => b.diff - a.diff)
        this.setState({ myPlane })
    }

    hoverTr = (ID) => {
        return () => {
            if (RightColumn.currentHover === ID) return true
            RightColumn.currentHover = ID
            if (ID.indexOf("->") < 0) {//
                Layer.updateHighlightPoint_Edge(ID)
            } else {//
                Layer.updateHighlightEdge(ID)
            }

        }
    }

    leaveTr = (ID) => {
        return () => {
            if (RightColumn.currentHover === "") return true
            RightColumn.currentHover = ""
            Layer.cancelHighlight()
        }
    }


    render() {
        let { dataKind, myNodes, myEdges, myPlane } = this.state
        let title = "---" + dataKind
        let content = "" + dataKind + ""
        return (
            <div className="rightcolumn">
                <div className="card">
                    <h3>{title}</h3>
                    <input ref={this.nodeRef} type="text" name="firstname" placeholder={content}></input>
                    <button onClick={this.updateTable("node", this.nodeRef)}></button>
                    <div className="conclusion" style={{ height: "300px" }}>
                        <table className="myTable" border="1">
                            <thead>
                                <tr>
                                    <th></th>
                                    <th>ID</th>
                                    <th>{dataKind}</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    myNodes.map(node => {
                                        return (
                                            <tr key={node.ID} onMouseOver={this.hoverTr(node.ID)} onMouseLeave={this.leaveTr(node.ID)}>
                                                <td>{node.city}</td>
                                                <td>{node.dev}</td>
                                                <td>{node.value}</td>
                                            </tr>
                                        )
                                    })
                                }
                            </tbody>

                        </table>
                    </div>
                </div>

                <div className="card">
                    <h3>{title}</h3>
                    <input ref={this.edgeRef} type="text" name="firstname" placeholder={content}></input>
                    <button onClick={this.updateTable("edge", this.edgeRef)}></button>
                    <div className="conclusion" style={{ height: "300px" }}>
                        <table className="myTable" border="1">
                            <thead>
                                <tr>
                                    <th>1</th>
                                    <th>2</th>
                                    <th>{dataKind}</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    myEdges.map(edge => {
                                        return (
                                            <tr key={edge.ID} onMouseOver={this.hoverTr(edge.ID)} onMouseLeave={this.leaveTr(edge.ID)}>
                                                <td>{edge.source}</td>
                                                <td>{edge.target}</td>
                                                <td>{edge.value}</td>
                                            </tr>
                                        )
                                    })
                                }
                            </tbody>

                        </table>
                    </div>
                </div>

                <div className="card">
                    <h3>{title}</h3>
                    <input ref={this.doublePlaneRef} type="text" name="firstname" placeholder={content}></input>
                    <button onClick={this.updateTable("doublePlane", this.doublePlaneRef)}></button>
                    <div className="conclusion" style={{ height: "300px" }}>
                        <table className="myTable" border="1">
                            <thead>
                                <tr>
                                    <th></th>
                                    <th></th>
                                    <th>ID</th>
                                    <th>diff</th>
                                    <th>{dataKind}</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    myPlane.map(node => {
                                        return (
                                            <tr key={node.ID} onMouseOver={this.hoverTr(node.ID)} onMouseLeave={this.leaveTr(node.ID)}>
                                                <td>{node.prov}</td>
                                                <td>{node.city}</td>
                                                <td>{node.dev}</td>
                                                <td>{node.diff.toFixed(2)}</td>
                                                <td>{node.value}</td>
                                            </tr>
                                        )
                                    })
                                }

                            </tbody>

                        </table>
                    </div>
                </div>

            </div>
        )
    }
}