import React, { Component } from 'react'
import DeckGL from '@deck.gl/react'
import { LineLayer, PathLayer, ScatterplotLayer, TextLayer, PolygonLayer } from '@deck.gl/layers'
import { OrbitView } from "@deck.gl/core"
import PubSub from "pubsub-js"
import ComputeData from "./ComputeData"

export default class Map extends Component {

    highlightColor = "#44ffff";
    deHighlightColor = "#ffffff";
    hasDraw = {
        hasClick: [],
        hasConvert: []
    }

    constructor(props) {
        super(props);
        this.allLayers= {}
        this.state = {
            isFinish: false,
            progress: 0,
            layers: [],
            button: {
                dataKind: { traffic: true, bd: false },
                edgeKind: { EB: false, DC: true },
                plane: { A: true, B: true },
                netflow: false
            }
        };
        this.INITIAL_VIEW_STATE = {
            target: [965.7479834999999, 300.012138, 0],
            zoom: 1,
            rotationOrbit: 0,
            rotationX: 40,
            minRotationX: -90,
            maxRotationX: 90,
            minZoom: -10,
            maxZoom: 10
        }
    }

    componentDidMount() {
        this.token = PubSub.subscribe('data', (_, stateObj) => {
            this.setState(stateObj)
            console.log("", this.state)
            let { results } = this.state
            let allNodes = results.allNodes
            let maxNodeTraffic = 0, maxNodeBd = 0
            for (let attr in allNodes) {
                let node = allNodes[attr]
                let { traffic, bdUtilization } = node
                if (traffic > maxNodeTraffic) maxNodeTraffic = traffic
                if (bdUtilization > maxNodeBd) maxNodeBd = bdUtilization
            }
            this.maxNodeBd = maxNodeBd
            this.maxNodeTraffic = maxNodeTraffic
            let layers = this.initLayers()
            this.setState({ layers })
        });
    }

    componentWillUnmount() {
        PubSub.unsubscribe(this.token)
    }
    initLayers = () => {// topo graph ,netflowscatterplot polygon
        let newResults = []
        let strArr = []
        let { results, button, netflowResults } = this.state
        let { allNodes } = results
        let STR = [[], [], []]
        this.hasDraw = {
            hasClick: [],
            hasConvert: []
        }


        for (let kind in button["edgeKind"]) {
            if (button["edgeKind"][kind]) STR[0].push(kind)
        }
        for (let kind in button["dataKind"]) {
            if (button["dataKind"][kind]) STR[1].push(kind)
        }
        for (let kind in button["plane"]) {// A  B
            if (button["plane"][kind]) STR[2].push(kind)
        }

        for (let i = 0; i < STR[0].length; i++) {//edgeKind: EB DC
            let r1 = results[STR[0][i]]

            for (let j = 0; j < STR[1].length; j++) {//datakind : traffic bd
                let dataStr = STR[1][j]
                let planeStr = STR[2].length === 2 ? "AB" : (STR[2].length === 1 ? STR[2][0] : "")// A  B, AB
                if (planeStr === "") continue
                let attr = planeStr + "_" + dataStr
                newResults.push(r1[attr])
                strArr.push(STR[0][i] + "_" + attr)
            }
        }

        let layers = []
        let scatterSet = new Set()
        let scatterData = { bd: [], traffic: [] }
        this.allLayers["pathLayer"] = layers

        //pathlayer, scatterplot
        for (let i = 0; i < newResults.length; i++) {
            let r = newResults[i]
            let id = String(strArr[i])
            let p = this.getPathLayer(r, id)
            layers.push(p)
            r.forEach(path => {
                let starr = path.info.split(" ")[0].split("->")
                let source = starr[0]
                let target = starr[1]
                if (!scatterSet.has(source)) {
                    scatterSet.add(source)
                    let node = allNodes[source]
                    let { x, y, z } = node.coordinates
                    let { traffic, bdUtilization, type } = node
                    let color = type.indexOf("C") > 0 ? [255, 0, 0] : [0, 0, 255]
                    let r_t = this.getScatterRadius(traffic, "traffic")
                    let r_b = this.getScatterRadius(bdUtilization, "bd")
                    scatterData.bd.push({ info: source, position: [x, y, z], value: bdUtilization, radius: r_b, fillColor: color })
                    scatterData.traffic.push({ info: source, position: [x, y, z], value: traffic, radius: r_t, fillColor: color })
                }
                if (!scatterSet.has(target)) {
                    scatterSet.add(target)
                    let node = allNodes[target]
                    let { x, y, z } = node.coordinates
                    let { traffic, bdUtilization, type } = node
                    let color = type.indexOf("C") > 0 ? [255, 0, 0] : [0, 0, 255]
                    let r_t = this.getScatterRadius(traffic, "traffic")
                    let r_b = this.getScatterRadius(bdUtilization, "bd")
                    scatterData.bd.push({ info: target, position: [x, y, z], value: bdUtilization, radius: r_b, fillColor: color })
                    scatterData.traffic.push({ info: target, position: [x, y, z], value: traffic, radius: r_t, fillColor: color })
                }

            })

        }
        let datakind = STR[1].length !== 0 ? STR[1][0] : ""
        let netflowLayers = []
        this.allLayers["netflowLayer"] = netflowLayers
        if (button["netflow"]) {
            if (button["edgeKind"]["EB"] === false && button["edgeKind"]["DC"] === false) {

            } else {
                if (button["edgeKind"]["EB"]) {
                    netflowLayers.push(this.getPathLayer(netflowResults.edge["EB"], "netflow-EB"))
                }
                if (button["edgeKind"]["DC"]) {
                    netflowLayers.push(this.getPathLayer(netflowResults.edge["DC"], "netflow-DC"))
                }
                //
                let nodes = netflowResults.nodes
                for (let attr in nodes) {
                    if (!scatterSet.has(attr)) {
                        let position = nodes[attr].coordinates
                        let value = nodes[attr].value
                        let radius = nodes[attr].radius
                        let fillColor = nodes[attr].fillColor
                        scatterData[datakind].push({ info: attr, position, value, radius, fillColor })
                    }
                }
            }
        }

        //netflow
        let ringLayers = []
        this.allLayers["ringLayer"] = ringLayers
        let ringID = 0
        if (button["netflow"]) {
            netflowResults["edge"]["Ring"].forEach(line => {
                let { city, color, coordinates, value } = line
                let radius = netflowResults["nodes"][city]["radius"] * 1.5
                let z = coordinates[2]
                let x0 = coordinates[0] - radius
                let y0 = coordinates[1] - radius

                let x1 = coordinates[0] + radius
                let y1 = coordinates[1] - radius

                let x2 = coordinates[0] + radius
                let y2 = coordinates[1] + radius

                let x3 = coordinates[0] - radius
                let y3 = coordinates[1] + radius

                let polygonData = [{ contour: [[x0, y0, z], [x1, y1, z], [x2, y2, z], [x3, y3, z], [x0, y0, z]] }]
                ringLayers.push(this.getCircleLayer(polygonData, "ring" + ringID, [0, 0, 255]))
                ringID++
            })
        }



        let minX = 1000000, maxX = 0
        let minY = 1000000, maxY = 0
        scatterData[datakind].forEach(line => {
            let x = line.position[0]
            let y = line.position[1]
            if (minX > x) minX = x
            if (maxX < x) maxX = x
            if (minY > y) minY = y
            if (maxY < y) maxY = y
        })

        layers = [...layers, ...netflowLayers, ...ringLayers]


        //scatterPlot Layer
        let scatterLayer = datakind === "" ? null : this.getScatterPlotLayer(scatterData[datakind], "scatter-plot")
        this.scatterData = scatterData[datakind]
        let textLayer = this.getTextLayer(scatterData[datakind], "text")

        this.allLayers["scatterLayer"] = [scatterLayer]
        layers.push(scatterLayer)
        this.allLayers["textLayer"] = [textLayer]
        layers.push(textLayer)


        //
        let A0 = ComputeData.typeHeight["A_N"]
        let A1 = ComputeData.typeHeight["A_C"]
        let B0 = ComputeData.typeHeight["B_C"]
        let B1 = ComputeData.typeHeight["B_N"]
        let polygonData0 = [{ contour: [[minX, minY, A0], [minX, maxY, A0], [maxX, maxY, A0], [maxX, minY, A0], [minX, minY, A0]] }]
        let polygonData1 = [{ contour: [[minX, minY, A1], [minX, maxY, A1], [maxX, maxY, A1], [maxX, minY, A1], [minX, minY, A1]] }]
        let polygonData2 = [{ contour: [[minX, minY, B0], [minX, maxY, B0], [maxX, maxY, B0], [maxX, minY, B0], [minX, minY, B0]] }]
        let polygonData3 = [{ contour: [[minX, minY, B1], [minX, maxY, B1], [maxX, maxY, B1], [maxX, minY, B1], [minX, minY, B1]] }]
        let polygonArr = []
        this.allLayers["polygonLayer"] = polygonArr

        const normalPlaneColor = [0, 0, 255, 10]
        const corePlaneColor = [255, 0, 0, 10]

        if (STR[2].length === 2 && newResults.length !== 0) {
            polygonArr.push(this.getPolygonLayer(polygonData0, "p0", normalPlaneColor))
            polygonArr.push(this.getPolygonLayer(polygonData1, "p1", corePlaneColor))
            polygonArr.push(this.getPolygonLayer(polygonData2, "p2", corePlaneColor))
            polygonArr.push(this.getPolygonLayer(polygonData3, "p3", normalPlaneColor))
        } else if (STR[2].length === 1 && newResults.length !== 0) {
            if (STR[2][0] === "A") {
                polygonArr.push(this.getPolygonLayer(polygonData0, "p0", normalPlaneColor))
                polygonArr.push(this.getPolygonLayer(polygonData1, "p1", corePlaneColor))
            } else {
                if (button["netflow"]) {
                    polygonArr.push(this.getPolygonLayer(polygonData0, "p0", normalPlaneColor))
                    polygonArr.push(this.getPolygonLayer(polygonData1, "p1", corePlaneColor))
                }
                polygonArr.push(this.getPolygonLayer(polygonData2, "p2", corePlaneColor))
                polygonArr.push(this.getPolygonLayer(polygonData3, "p3", normalPlaneColor))
            }
        } else {
            if (button["netflow"] && STR[0].length !== 0) {
                polygonArr.push(this.getPolygonLayer(polygonData0, "p0", normalPlaneColor))
                polygonArr.push(this.getPolygonLayer(polygonData1, "p1", corePlaneColor))
            } else {
                polygonArr = []
            }

        }

        layers = [...layers, ...polygonArr]
        return layers
    }

    updateButtonState = (id) => {
        return () => {
            let button = this.state.button
            if (id === "traffic" || id === "bd") {
                let other = id === "traffic" ? "bd" : "traffic"
                button.dataKind[other] = !button.dataKind[other]
                button.dataKind[id] = !button.dataKind[id]
                this.setState({ button })
            } else if (id === "EB" || id === "DC") {
                button.edgeKind[id] = !button.edgeKind[id]
                this.setState({ button })
            } else if (id === "A" || id === "B") {
                button.plane[id] = !button.plane[id]
                this.setState({ button })
            } else {//netflow
                button.netflow = !button.netflow
                this.setState({ button })
            }
            let layers = this.initLayers()
            this.setState({ layers })
            this.combineLayers()
        }
    }

    getButtonColor = (id) => {
        let button = this.state.button
        let color
        if (id === "traffic" || id === "bd") {
            color = button.dataKind[id] ? this.highlightColor : this.deHighlightColor

        } else if (id === "EB" || id === "DC") {
            color = button.edgeKind[id] ? this.highlightColor : this.deHighlightColor
        } else if (id === "A" || id === "B") {
            color = button.plane[id] ? this.highlightColor : this.deHighlightColor
        } else {//netflow
            color = this.state.button.netflow ? this.highlightColor : this.deHighlightColor
        }
        return color
    }

    getScatterRadius = (value, kind) => {//computeData
        let maxRadius = 8
        let minRadius = 2
        if (kind === "traffic") {
            let t = value / this.maxNodeTraffic * 10
            t = t > 1 ? 1 : t
            let radius = parseInt((maxRadius - minRadius) * t + minRadius)
            return radius
        } else {
            let t = value / this.maxNodeBd * 10
            t = t > 1 ? 1 : t
            let radius = (maxRadius - minRadius) * t + minRadius
            return radius
        }
    }



    render() {
        const { isFinish, layers } = this.state

        return (
            <div>
                {!isFinish ? <h2>, ...</h2> :
                    <div>
                        <div id="option">
                            <button onClick={this.updateButtonState("traffic")} style={{ backgroundColor: this.getButtonColor("traffic") }}>Traffic</button>
                            <button onClick={this.updateButtonState("bd")} style={{ backgroundColor: this.getButtonColor("bd") }}>Bandwidth Utilization</button>
                            <button onClick={this.updateButtonState("EB")} style={{ backgroundColor: this.getButtonColor("EB") }}>Edge Bundling</button>
                            <button onClick={this.updateButtonState("DC")} style={{ backgroundColor: this.getButtonColor("DC") }}>Direct Connect</button>
                            <button onClick={this.updateButtonState("A")} style={{ backgroundColor: this.getButtonColor("A") }}>A Plane</button>
                            <button onClick={this.updateButtonState("B")} style={{ backgroundColor: this.getButtonColor("B") }}>B Plane</button>
                            <button onClick={this.updateButtonState("netflow")} style={{ backgroundColor: this.getButtonColor("netflow") }}>Netflow</button>
                        </div>
                        <div id="map">
                            <DeckGL
                                views={new OrbitView({
                                    orbitAxis: 'Z',
                                })}
                                initialViewState={this.INITIAL_VIEW_STATE}
                                controller={true}
                                layers={[...layers]}//{[this.getPathLayer(this.state.graphData,this.getLineColorCB)]}
                            />
                        </div>

                    </div>
                }
            </div>

        )
    }
    getPathLayer = (data, layerID, opacity) => {//PathLayerd.path, d.color

        //2D
        data = this.filter2D(data)

        return new PathLayer({
            id: layerID,
            data: [...data],
            pickable: true,
            widthScale: 1,
            widthMinPixels: 0.1,
            widthMaxPixels: 3,
            getPath: d => { return d.path },
            getColor: d => d.color,
            getWidth: d => { return d.width },
            opacity: opacity === undefined ? 0.2 : opacity,
            billboard: true,
            autoHighlight: true,
            highlightColor: [255, 0, 0],
            onClick: (d) => { this.showInfo(d) }//this.convertEdge(d)  this.showInfo(d)
        });
    }

    getScatterPlotLayer = (data, layerID) => {//ScatterPlotLayerd.fillColor, d.position, d.radius,
        return new ScatterplotLayer({
            id: layerID,
            data: [...data],
            getFillColor: d => d.fillColor,
            getLineColor: [0, 0, 0],
            getPosition: d => { let a = d.position; return d.position },//[a[0]*6, a[1]*4,a[2]]},//{ return [...d.xyz] },
            getRadius: d => { return d.radius },
            lineWidthMinPixels: 0,
            radiusMaxPixels: 100,
            radiusMinPixels: 1,
            radiusScale: 1,
            stroked: false,
            opacity: 1,
            pickable: true,
            autoHighlight: true,
            highlightColor: [255, 215, 0],
            onHover: (info) => this.highlightPoint_Edge(info),
            onClick: (d,event) => {this.clickScatter(d, event)}
        })
    }

    getTextLayer = (data, layerID) => {//TextLayerd.position, d.name
        return new TextLayer({
            id: layerID,
            data: [...data],
            pickable: false,
            getPosition: d => { let a = d.position; return [a[0], a[1], a[2] + 2] },
            getText: d => { return d.info.split(",")[0] },
            characterSet: "auto",
            getSize: 10,
            getAngle: 0,
            sizeScale: 1.2,
            sizeMinPixels: 10,
            //sizeMaxPixels:1,
            getTextAnchor: 'middle',
            getAlignmentBaseline: 'center'
        });
    }

    getPolygonLayer = (data, layerID, color) => {
        return new PolygonLayer({
            id: layerID,
            data: data,
            pickable: false,
            stroked: false,
            filled: true,
            wireframe: true,
            lineWidthMinPixels: 1,
            getPolygon: d => d.contour,
            getFillColor: color,
            getLineColor: [80, 80, 80],
            getLineWidth: 0,
            opacity: 1
        })
    }

    getCircleLayer = (data, layerID, color) => {
        return new PolygonLayer({
            id: layerID,
            data: data,
            pickable: true,
            stroked: true,
            filled: true,
            wireframe: true,
            lineWidthMinPixels: 1,
            getPolygon: d => d.contour,
            //getElevation: d => d.population / d.area / 10,
            getFillColor: [255, 255, 0],
            getLineColor: [255, 255, 0],
            getLineWidth: 1,
            opacity: 1
        })
    }

    convertEdge = (event) => {//DCEB, 
        let { layer, object } = event
        let edgeKind = layer.id.split("_")[0]// EBDC
        let attr = layer.id.replace(String(edgeKind) + "_", "")
        if (edgeKind !== "EB" && edgeKind !== "DC") { alert("edgeKind" + String(layer.id)) }
        let ftid = object.info.split(" ")[0]
        if (ftid === undefined || ftid.indexOf("->") < 0) { alert("info" + String(object.info)) }
        let dataKind = this.state.button.dataKind.traffic ? "traffic" : "bd"
        let otherKind = edgeKind === "EB" ? "DC" : "EB"
        let { results } = this.state
        let data = results[otherKind][attr]
        for (let i = 0; i < data.length; i++) {
            let info = data[i].info
            if (info.indexOf(ftid) > -1) {
                let layer = this.getPathLayer([data[i]], ftid, false)
                let layers = this.state.layers
                layers = [layer, ...layers]
                this.setState({ layers })
                break
            }
        }
    }
    showInfo = (event) => {
        let info = event.object.info
        info.replace("->", "<-->")
        alert(String(info))
    }

    filter2D = (data) => {
        let newData = []
        data.forEach(line => {
            let path = line.path
            if (path[0][2] !== path[path.length - 1][2]) {
                newData.push(line)
            }
        })
        return newData
    }

    preState = false
    highlightPoint_Edge = (info) => {
        let netflow = this.state.button.netflow
        if(netflow)return null
        if (info.picked && !this.preState) {
            this.preState = true
            let scatterInfo = info.object.info
            this.updateHighlightPoint_Edge(scatterInfo)
        }
        if (!info.picked && this.preState) {
            this.preState = false
            this.cancelHighlight()
        }
    }
    updateHighlightPoint_Edge(scatterInfo) {
        const { results } = this.state
        const { DC, EB } = results
        let {layers} = this.state
        let dataKind = this.state.button.dataKind.traffic ? "traffic" : "bd"
        let plane = !this.state.button.plane.A ? (!this.state.button.plane.B ? "" : "B") : (!this.state.button.plane.B ? "A" : "AB")
        let edgeKind = this.state.button.edgeKind.EB ? "EB" : "DC"
        let myData = edgeKind === "EB" ? EB : DC
        if (plane === "") { return true }
        let data = myData[plane + "_" + dataKind]
        if(data === undefined){console.log("data",plane + "_" + dataKind)}
        let highlightEdges = []
        let highlightScatter = []
        let ScatterObj = {}
        data.forEach(line=>{
            let info = line.info
            if(info.indexOf(scatterInfo)>-1){
                let {info, path, value, width} = line
                highlightEdges.push({color:[255,0,0], info, path, value, width})
                let cityArr = info.split(" ")[0].split("->")
                let source = cityArr[0], target = cityArr[1]
                ScatterObj[source] = true
                ScatterObj[target] = true
            }
        })
        
        this.scatterData.forEach(line=>{
            if(ScatterObj[line.info]===true){
                let {info, radius, value} = line
                let position = [...line.position]
                let fillColor = [255, 215, 0]
                highlightScatter.push({fillColor, info, position, radius, value})
            }
        })
        
        let pathLayer = this.getPathLayer(highlightEdges, "highlightLayer", 0.5)
        this.allLayers["highlightPahtLayer"] = [pathLayer]

        let scatterLayer = this.getScatterPlotLayer(highlightScatter,"highlightScatter")
        this.allLayers["highlightScatterLayer"] = [scatterLayer]

        this.combineLayers()
    }
    cancelHighlight=()=>{
        this.allLayers["highlightPahtLayer"] = []
        this.allLayers["highlightScatterLayer"] = []
        this.combineLayers()
    }
    combineLayers = ()=>{
        let layers = []
        //pathLayernetflowLayerringLayer
        layers=[...this.allLayers.pathLayer, ...this.allLayers.netflowLayer, ...this.allLayers.ringLayer, ...this.allLayers.scatterLayer]
        //highlightLayer
        for(let attr in this.allLayers){
            if(attr.indexOf("highlight")>-1){
                layers = [...layers, ...this.allLayers[attr]]
            }
        }
        //rightClickLayer
        for(let attr in this.rightClickLayers){
            if(this.rightClickLayers[attr] !== null){
                layers.push(this.rightClickLayers[attr])
            }
        }
        layers = [...layers, ...this.allLayers["textLayer"], ...this.allLayers["polygonLayer"]]
        this.setState({layers})
    }


    rightClickScatter = {}
    rightClickLayers = {}
    clickScatter = (d,event)=>{
        if(event.leftButton){
            alert(d.object.info + " " + String(d.object.value))
        }else{
            let scatterID = d.object.info
            if(this.rightClickScatter[scatterID] === undefined) this.rightClickScatter[scatterID] = false
            this.rightClickScatter[scatterID] = !this.rightClickScatter[scatterID]//falsetrue
            if(!this.rightClickScatter[scatterID]){
                this.rightClickLayers[scatterID] = null
                this.combineLayers()
                return true
                //**********TODO */
            }
            let edgeKind = this.state.button.edgeKind.EB ? "EB" : "DC"
            if(this.state.button.edgeKind.EB && this.state.button.edgeKind.DC)return null
            const {DC, EB} = this.state.results;
            let dataKind = this.state.button.dataKind.traffic ? "traffic" : "bd"
            let plane = !this.state.button.plane.A ? (!this.state.button.plane.B ? "" : "B") : (!this.state.button.plane.B ? "A" : "AB")
            if (plane === "") { return true }
            let myData = edgeKind === "EB" ? DC : EB
            let data = myData[plane + "_" + dataKind]
            let edgesData = []
            data.forEach(line=>{
                let {info} = line
                if(info.indexOf(scatterID)>-1)edgesData.push(line)
            })
            let layer = this.getPathLayer(edgesData,"rightClick"+String(scatterID))
            this.rightClickLayers[scatterID] = layer
            this.combineLayers()
            
        }
    }

}